// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import axios from 'axios'
import Vue from 'vue'
import App from './App'
import router from './router'
import 'babel-polyfill'
import ElementUI from 'element-ui'
import locale from 'element-ui/lib/locale/lang/zh-CN'
import 'element-ui/lib/theme-chalk/index.css'
import 'element-ui/lib/theme-chalk/display.css'
import i18n from './i18n'
import './styles/variables.css'
import './styles/common.css';
import {checkLogin, linkTo, getApi, postApi,message} from './common/utils'
import config from '../config/'
import {AV} from './common/api';

Vue.use(ElementUI, {locale})
// Vue.use(utils);
Vue.config.productionTip = false
router.beforeEach((to, from, next) => {
  const currentUser = AV.User.current();
  if (to.name !== 'Login' && to.name !== 'Register' && !currentUser) {
    next('/Login')
  } else {
    next()
  }
})
Vue.prototype.$http = axios
Vue.prototype.$linkTo = linkTo
Vue.prototype.$post = postApi
Vue.prototype.$get = getApi
Vue.prototype.$log = console.log
Vue.prototype.$loadingConfig = config.ls.loading
Vue.prototype.$config = config
Vue.prototype.$msg=message;
Vue.prototype.$t=function(str){
  return str;
}
// router.beforeEach((to, from, next) => {
//   if (auth()) {
//     next()
//   }
// })

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,

  components: {
    App
  },

  // i18n,
  template: '<App/>'
})



