<template>
  <article>
    <div style="height: 100px;width: 100px;float: left;background: lightblue">我是一个左浮动的元素</div>
    <div style="width: 200px; height: 200px;background: #eee">我是一个没有设置浮动,
也没有触发 BFC 元素, width: 200px; height:200px; background: #eee;</div>
    <div @click="propEvent"><button>事件委托，冒泡事件</button></div>
  </article>
</template>

<script>
    export default {
        computed: {},
        methods: {
          propEvent(e){
              console.log(e);
          }
        },
        data(){
            return {index:1}
        },
        created(){

        },
        mounted(){

        },
    }
</script>

<style scoped>
  div{
    border: var(--border);
    margin:50px;
  }
</style>



