<template>
  <el-container class="ts">
    <el-container>
      <el-main class="cm-vertical cm-center cm-full-h cm-flex">
        <div class="cm-full-w cm-horizon cm-space-b">
          <el-input type="text" class="ts-input-add" v-model="taskName" placeholder="Add Task">
            <el-select v-model="taskType" class="ts-type" slot="prepend" placeholder="任务类型">
              <el-option label="ISSUE" :value="1"></el-option>
              <el-option label="CR" :value="2"></el-option>
              <el-option label="TODO" :value="3"></el-option>
            </el-select>
            <el-button
              slot="append"
              @click="addTask"
              icon="el-icon-document-add"
              class="ts-input-btn"
            >添加任务</el-button>
          </el-input>
          <el-button type="danger" plain icon="el-icon-s-tools" @click="infoDrawer=true">菜单</el-button>
        </div>

        <el-tabs
          class="ts-tabs cm-vertical cm-flex cm-overflow-h"
          v-model="activeTab"
          @tab-click="tabClick"
        >
          <el-tab-pane label="进行中" name="taskUnfinished">
            <el-table
              :data="taskList"
              style="width: 100%;height: 100%;"
              :row-class-name="tableRowClassName"
            >
              <el-table-column prop="taskType" width="40">
                <template slot-scope="scope">
                  <i :class="getTaskIcon(scope)"></i>
                </template>
              </el-table-column>
              <el-table-column prop="taskName" label="任务"></el-table-column>
              <el-table-column label="状态" prop="taskStatus" width="110">
                <template slot-scope="scope">
                  <el-select
                    v-model="scope.row.taskStatus.taskStatusId"
                    size="mini"
                    @change="setStatus(scope)"
                  >
                    <el-option
                      v-for="item in statusOption"
                      :key="item.value"
                      :label="item.label"
                      :value="item.value"
                    ></el-option>
                  </el-select>
                </template>
              </el-table-column>
              <el-table-column
                prop="createdAt"
                sortable
                :formatter="formatDate"
                :width="style.operateWidth"
                label="创建日期"
              ></el-table-column>
              <el-table-column
                prop="startTime"
                sortable
                :formatter="formatDate"
                :width="style.operateWidth"
                label="开始日期"
              ></el-table-column>
              <el-table-column width="200" align="center">
                <template slot="header" slot-scope="scope">
                  <el-input
                    v-model="search"
                    size="mini"
                    placeholder="关键字搜索"
                    @change="searchTask"
                    v-on:keyup.enter="searchTask"
                  >
                    <el-select
                      v-model="searchType"
                      @change="searchTask"
                      class="ts-type2"
                      slot="prepend"
                      placeholder="任务类型"
                    >
                      <el-option label="标题" :value="1"></el-option>
                      <el-option label="详情" :value="2"></el-option>
                    </el-select>
                  </el-input>
                </template>
                <template slot-scope="scope">
                  <el-button-group>
                    <el-button size="mini" @click.stop="setTopLevel(scope)" icon="el-icon-upload2"></el-button>
                    <el-button size="mini" @click.stop="editDetail(scope)" icon="el-icon-edit"></el-button>
                    <el-button size="mini" @click.stop="handleDelete(scope)" icon="el-icon-delete"></el-button>
                  </el-button-group>
                </template>
              </el-table-column>
            </el-table>
            <el-pagination
              small
              class="cm-padding-v cm-horizon cm-center"
              :page-sizes="[10, 20, 30, 40]"
              layout="prev, pager, next,sizes"
              @current-change="queryTaskList"
              @prev-click="queryTaskList"
              @next-click="queryTaskList"
              @size-change="changePageSize"
              :total="totalCount"
            ></el-pagination>
          </el-tab-pane>

          <!--------------已完成--------------->

          <el-tab-pane label="已完成" name="taskFinished">
            <el-table
              :data="taskList"
              style="width: 100%;height: 100%;"
              @expand-change="editDetail"
              :row-class-name="tableRowClassName"
            >
              <el-table-column type="expand" width="40">
                <template slot-scope="props">
                  <el-form label-position="left" class="cm-flex ts-expand-left">
                    <el-form-item label="任务类型">
                      <span>{{ getTaskType(props)}}</span>
                    </el-form-item>
                    <el-form-item label="创建日期">
                      <span>{{ formatDate(null, null, props.row.createdAt) }}</span>
                    </el-form-item>
                    <el-form-item label="开始日期">
                      <span>{{ formatDate(null, null, props.row.startTime) }}</span>
                    </el-form-item>
                    <el-form-item label="完成日期">
                      <span>{{ formatDate(null, null, props.row.finishTime) }}</span>
                    </el-form-item>
                    <el-form-item label="任务时长">
                      <span>{{getTaskTime(props.row)}}</span>
                    </el-form-item>
                  </el-form>
                  <el-form
                    label-width="80px"
                    label-position="left"
                    class="cm-flex ts-expand-right"
                    v-model="Model[currentTaskType]"
                  >
                    <el-form-item v-for="item in Model[currentTaskType]" :label="item.title">
                      <el-input
                        size="mini"
                        v-if="item.type!=='select'"
                        v-model="item.value"
                        :type="item.type?item.type:'textarea'"
                      ></el-input>
                      <el-select class="cm-full-h" size="mini" v-else v-model="item.value">
                        <el-option
                          v-for="it in item.options"
                          :key="it.value"
                          :label="it.label"
                          :value="it.value"
                        ></el-option>
                      </el-select>
                    </el-form-item>
                    <el-form-item>
                      <el-button size="small" type="primary" @click="submitEdit">修 改</el-button>
                    </el-form-item>
                  </el-form>
                </template>
              </el-table-column>
              <el-table-column prop="taskName" label="任务"></el-table-column>
              <el-table-column label="状态" prop="taskStatus" width="110">
                <template slot-scope="scope">
                  <el-select v-model="scope.row.taskStatus.taskStatusId" size="mini" disabled>
                    <el-option
                      v-for="item in statusOption"
                      :key="item.value"
                      :label="item.label"
                      :value="item.value"
                    ></el-option>
                  </el-select>
                </template>
              </el-table-column>
              <el-table-column
                prop="startTime"
                sortable
                :formatter="formatDate"
                :width="style.operateWidth"
                label="开始日期"
              ></el-table-column>
              <el-table-column
                prop="finishTime"
                sortable
                :formatter="formatDate"
                :width="style.operateWidth"
                label="完成日期"
              ></el-table-column>
              <el-table-column width="200" align="center">
                <template slot="header" slot-scope="scope">
                  <el-input
                    v-model="search"
                    size="mini"
                    placeholder="关键字搜索"
                    @change="searchTask"
                    v-on:keyup.enter="searchTask"
                  >
                    <el-select
                      v-model="searchType"
                      @change="searchTask"
                      class="ts-type2"
                      slot="prepend"
                      placeholder="任务类型"
                    >
                      <el-option label="标题" :value="1"></el-option>
                      <el-option label="详情" :value="2"></el-option>
                    </el-select>
                  </el-input>
                </template>
                <template slot-scope="scope" class="cm-align-c">
                  <el-button-group>
                    <el-button size="mini" @click.stop="handleDelete(scope)" icon="el-icon-delete"></el-button>
                  </el-button-group>
                </template>
              </el-table-column>
            </el-table>
            <el-pagination
              small
              class="cm-padding-v cm-horizon cm-center"
              :page-sizes="[10, 20, 30, 40]"
              layout="prev, pager, next,sizes"
              @current-change="queryTaskList"
              @prev-click="queryTaskList"
              @next-click="queryTaskList"
              @size-change="changePageSize"
              :total="totalCount"
            ></el-pagination>
          </el-tab-pane>
        </el-tabs>
      </el-main>
      <el-drawer :visible.sync="infoDrawer" size="20%" direction="rtl">
        <el-aside width="100%" class="cm-full-h cm-padding cm-vertical cm-space-b">
          <div>
            <h4>
              <i class="el-icon-info"></i>提示
            </h4>
            <br />
            <el-collapse v-model="activeNames">
              <el-collapse-item title="如何新增任务" name="1">
                <div class="ts-aside-text">左侧输入框中，下拉选择任务类型，输入任务名称，点击“添加任务”按钮，添加成功后可以选择是否立刻开始任务</div>
              </el-collapse-item>
              <el-collapse-item title="任务的状态" name="2">
                <div class="ts-aside-text">
                  任务有4个状态：准备做，进行中，测试中，已完成。
                  选择进行中，则系统记录任务开始时间。选择已完成，则系统记录任务的结束时间，任务终止，
                  可以在已完成任务中点击最左侧“>”查看任务时长
                </div>
              </el-collapse-item>
              <el-collapse-item title="设置优先级" name="3">
                <div
                  class="ts-aside-text"
                >点击“Top Level”按钮，可以将该任务置顶，置顶的前三个任务分别显示红色第一优先级，橙色第二优先级，绿色第三优先级</div>
              </el-collapse-item>
              <el-collapse-item title="编辑和删除任务" name="4">
                <div class="ts-aside-text">如需要修改任务名等信息，可以删除重新创建，点击编辑按钮可以编辑cr和issue的人天，细节，修改代码等</div>
              </el-collapse-item>
            </el-collapse>
          </div>
          <div>
            <h3 class="cm-padding-v">welcome,{{currentUser.username}}</h3>
            <el-button-group>
              <el-button type="warning" size="mini" @click="createActivationCode" v-if="admin">生成激活码</el-button>
              <el-button type="warning" size="mini" @click="logOut">注销登录</el-button>
            </el-button-group>
          </div>
        </el-aside>
      </el-drawer>
    </el-container>
    <el-footer></el-footer>
    <aside>
      <el-dialog
        class="ts-dialog"
        width="800px"
        :fullscreen="true"
        title="编辑任务"
        :visible.sync="dialogEdit"
        :close-on-click-modal="false"
      >
        <el-form :inline="true" label-width="80px" :model="Model[currentTaskType]">
          <el-form-item v-for="item in Model[currentTaskType]" :label="item.title">
            <el-input
              size="mini"
              v-if="item.type!=='select'"
              v-model="item.value"
              :type="item.type?item.type:'textarea'"
              autocomplete="off"
            ></el-input>
            <el-select size="mini" v-else v-model="item.value">
              <el-option
                v-for="it in item.options"
                :key="it.value"
                :label="it.label"
                :value="it.value"
              ></el-option>
            </el-select>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer cm-horizon cm-center">
          <el-button size="small" @click="dialogEdit = false">取 消</el-button>
          <el-button size="small" type="primary" @click="submitEdit">确 定</el-button>
        </div>
      </el-dialog>
    </aside>
  </el-container>
</template>

<script>
import { AV } from "../../common/api";
import Model from "../../model";
import {
  saveLocalData,
  getLocalData,
  filterObject,
  uuid
} from "../../common/utils";
export default {
  components: {},
  computed: {
    /*getTaskList(){
       return this.taskList.filter((data) => {
       if (this.search) {
       return data.taskName.toLowerCase().includes(this.search.toLowerCase());
       } else {
       return data;
       }
       })
       }*/
  },
  created() {},
  mounted() {
    this.queryStatusList(this.queryTaskList.bind(this));
    this.isAdmin();
    //      this.updateTaskListAll();
  },
  methods: {
    getTaskType(props) {
      if (props.row) {
        let taskType = props.row.taskType;
        switch (taskType) {
          case 1:
            return "ISSUE";
            break;
          case 2:
            return "CR";
            break;
          default:
            return "TODO";
        }
      } else {
        return "";
      }
    },
    tableRowClassName({ row, rowIndex }) {
      if (rowIndex === 0) {
        return "ts-bg-danger";
      } else if (rowIndex === 1) {
        return "ts-bg-warning";
      } else if (rowIndex === 2) {
        return "ts-bg-success";
      }
      return "";
    },
    addTask() {
      if (this.taskName) {
        AV.addData(
          "taskList",
          {
            taskName: this.taskName,
            taskType: this.taskType,
            createUser: this.createUser
          },
          res => {
            this.taskName = "";
            this.$confirm("创建成功!立刻开始任务?")
              .then(data => {
                const { objectId } = res;
                let scope = {
                  row: { taskStatus: { taskStatusId: 1 }, objectId }
                };
                this.setStatus(scope);
              })
              .catch(e => {
                this.queryTaskList();
              });
          }
        );
      }
    },
    queryTaskList(currentPage = 1) {
      let taskStatusObjectId = filterObject(
        this.statusOption,
        { key: "taskStatusId", value: 2 },
        "objectId"
      );
      const taskStatus = AV.Object.createWithoutData(
        "taskStatus",
        taskStatusObjectId
      );
      let pageCount = this.pageSize;
      let skip = (currentPage - 1) * pageCount;
      //未完成任务按order升序排列
      let condition = [
        { operator: "==", data: [{ createUser: this.createUser }] },
        { operator: "<>", data: [{ taskStatus }] },
        { operator: "limit", data: [{ count: pageCount }] },
        { operator: "skip", data: [{ count: skip }] },
        { operator: "order", data: [{ asc: "order" }] },
        { operator: "include", data: [{ field: "taskStatus" }] }
      ];
      //已完成任务按完成日期倒序排列
      if (this.activeTab === "taskFinished") {
        condition = [
          {
            operator: "==",
            data: [{ taskStatus }, { createUser: this.createUser }]
          },
          { operator: "limit", data: [{ count: pageCount }] },
          { operator: "skip", data: [{ count: skip }] },
          { operator: "order", data: [{ desc: "finishTime" }] },
          { operator: "include", data: [{ field: "taskStatus" }] }
        ];
      }
      if (this.search) {
        if (this.searchType === 1) {
          condition.push({
            operator: "like",
            data: [{ taskName: this.search }]
          });
        } else {
          condition.push({ operator: "like", data: [{ detail: this.search }] });
        }
      }

      AV.queryData("taskList", condition).then(data => {
        this.taskList = data;
      });

      AV.queryData("taskList", condition, true).then(totalCount => {
        this.totalCount = totalCount;
      });
    },
    queryStatusList(callback) {
      const taskStatusList = getLocalData("taskStatusList");
      if (taskStatusList) {
        this.statusOption = taskStatusList;
        callback();
      } else {
        AV.queryData("taskStatus")
          .then(data => {
            data.forEach(item => {
              item.value = item.taskStatusId;
              item.label = item.taskStatusName;
            });
            this.statusOption = data;
            saveLocalData("taskStatusList", data);
          })
          .then(() => {
            callback();
          });
      }
    },
    handleDelete(scope) {
      const { objectId } = scope.row;
      this.$confirm("确认删除任务?")
        .then(data => {
          AV.deleteData("taskList", objectId).then(res => {
            if (res) {
              this.queryTaskList();
            } else {
              this.$msg("删除失败!");
            }
          });
        })
        .catch(e => {});
    },
    tabClick() {
      this.queryTaskList();
      this.restDetail();
      this.search = "";
    },
    setStatus(scope) {
      const { taskStatus, objectId } = scope.row;
      const { taskStatusId } = taskStatus;
      let extraData = {};
      const now = new Date();
      if (taskStatusId === 1) {
        extraData = { startTime: now };
      }
      if (taskStatusId === 2 || taskStatusId === 3) {
        extraData = { finishTime: now };
      }
      let taskStatusObjectId = filterObject(
        this.statusOption,
        {
          key: "taskStatusId",
          value: taskStatusId
        },
        "objectId"
      );
      const updateData = {
        tableName: "taskList",
        data: Object.assign({}, { taskStatus: "[RELATED]" }, extraData),
        objectId
      };
      const relatedData = {
        tableName: "taskStatus",
        objectId: taskStatusObjectId
      };
      AV.updateData(updateData, relatedData)
        .then(res => {
          if (!res) {
            this.$msg("更新状态失败!", 4);
            return;
          }
          this.queryTaskList();
        })
        .catch(e => {
          this.$msg("更新状态失败!", 4);
        });
    },
    formatDate(row, column, cellValue, index) {
      let data = cellValue;
      if (cellValue) {
        const d = new Date(cellValue);
        data = new Date(d.getTime() + 8 * 60 * 60 * 1000);
        data = data.toJSON();
      }
      if (data) {
        data = data.replace("T", " ");
        data = data.replace("Z", "");
        data = data.replace(/\.[0-9]+/, "");
      }
      return data;
    },
    setTopLevel(scope) {
      AV.queryData("taskList", [
        { operator: "<", data: [{ order: 1 }] },
        {
          operator: "==",
          data: [{ createUser: this.createUser }]
        }
      ])
        .then(res => {
          let min = 1;
          for (let item of res) {
            min = min > item["order"] ? item["order"] : min;
          }
          return min - 1;
        })
        .then(min => {
          const { objectId } = scope.row;
          const updateData = {
            tableName: "taskList",
            data: { order: min },
            objectId
          };
          AV.updateData(updateData)
            .then(res => {
              if (!res) {
                this.$alert("更新状态失败!");
                return;
              }
              this.queryTaskList();
            })
            .catch(e => {
              console.error(e);
            });
        });
    },
    getTaskIcon(scope) {
      const { taskType } = scope.row;
      if (taskType === 1) {
        return "el-icon-lightning";
      } else if (taskType === 2) {
        return "el-icon-sunny";
      } else {
        return "el-icon-cold-drink";
      }
    },
    getTaskTime(row) {
      const { startTime, finishTime } = row;
      let str = "",
        sec,
        min,
        hour,
        day;
      if (startTime && finishTime) {
        let diff = new Date(finishTime) - new Date(startTime);
        sec = Math.round(diff / 1000);
        min = Math.round(sec / 60);
        hour = Math.round(min / 60);
        day = Math.round(hour / 24);
        str += sec + "秒";
        str += " (" + min + "分)";
        str += " (" + hour + "时)";
        str += " (" + day + "天)";
        return str;
      }
      return "";
    },
    editDetail(scopeOrRow) {
      let data = scopeOrRow;
      if (this.activeTab === "taskUnfinished") {
        this.dialogEdit = true;
        data = scopeOrRow.row;
      }
      const { taskType, objectId, detail } = data;
      this.currentTaskType = taskType;
      this.currentObjectId = objectId;
      if (detail) {
        this.Model[taskType] = JSON.parse(detail);
      } else {
        this.Model[taskType] = AV.decodeData(Model)[taskType];
      }
    },
    submitEdit() {
      AV.updateData({
        tableName: "taskList",
        objectId: this.currentObjectId,
        data: { detail: JSON.stringify(this.Model[this.currentTaskType]) }
      })
        .then(res => {
          this.queryTaskList();
          this.$msg("修改成功", 1);
        })
        .catch(e => {
          this.$msg("获取数据失败," + e);
        })
        .finally(() => {
          if (this.activeTab === "taskUnfinished") {
            this.dialogEdit = false;
            this.restDetail();
          }
        });
    },
    getTaskDetail(scope) {
      return scope.row.detail
        ? JSON.parse(scope.row.detail)
        : this.Model[scope.row.taskType];
    },
    restDetail() {
      //clear dialog content,imported js will be modified,so need this function
      this.Model = AV.decodeData(Model);
    },
    changePageSize(pageSize) {
      this.pageSize = pageSize;
      this.queryTaskList();
    },
    searchTask() {
      this.queryTaskList();
      this.restDetail();
    },
    logOut() {
      AV.User.logOut();
      localStorage.clear();
      sessionStorage.clear();
      this.$linkTo("/Login");
    },
    createActivationCode() {
      this.$prompt("请输入被邀请者邮箱")
        .then(res => {
          let email = res.value;
          if (email) {
            const reg = /^([a-zA-Z]|[0-9])(\w|\-)+@[a-zA-Z0-9]+\.([a-zA-Z]{2,4})$/;
            if (!reg.test(email)) {
              this.$msg("邮箱格式不正确");
              return;
            }
            const code = uuid();
            AV.addData("activationCode", { email, code })
              .then(() => {
                this.$alert(`激活码是:${code}`).catch(() => {});
                console.log(`激活码是:${code}`);
              })
              .catch(e => {
                this.$msg(`无法创建激活码:${e}`).catch(() => {});
              });
          }
        })
        .catch(() => {});
    },
    isAdmin() {
      AV.isAdmin().then(is => {
        this.admin = is;
      });
    },
    updateTaskListAll() {
      const query = new AV.Query("taskList");
      query.find().then(tasks => {
        tasks.forEach(task => {
          console.log(AV.decodeData(task));
          const data = AV.decodeData(task);
          if (data.detail) {
            task.set("detail", JSON.stringify(data.detail));
          }
        });
        AV.Object.saveAll(tasks);
      });
    }
  },
  data() {
    const currentUser = AV.decodeData(AV.User.current());
    const { objectId } = currentUser;
    const createUser = AV.Object.createWithoutData("_User", objectId);
    return {
      activeTab: "taskUnfinished",
      taskName: null,
      taskType: 1,
      taskList: [],
      search: "",
      Model: AV.decodeData(Model),
      activeNames: [],
      style: {
        tableHeight: 620,
        operateWidth: 165
      },
      statusOption: [],
      dialogEdit: false,
      currentTaskType: 1,
      currentObjectId: "",
      formLabelWidth: "120px",
      pageSize: this.$config.common.pageNumber,
      totalCount: 0,
      admin: false,
      currentUser,
      createUser,
      searchType: 1,
      infoDrawer: false,
      finishedTaskType: "ISSUE"
    };
  }
};
</script>

<style>
.ts {
  width: 1000px;
  margin: 0 auto;
}

.ts > .el-footer {
  height: 0 !important;
}

.ts-type {
  width: 110px;
}

.ts-type2 {
  width: 70px;
}

.ts-input-add {
  width: 500px !important;
}

.ts-input-btn {
